// <Desktop-specific layout - secondary navigation never present
main > .documentation {
    @include media('<small') {
        @include default-margin();
    }
    grid-column: 1/-1;
    margin: 0;
}

// Generic styles
.documentation {
    .content {
        margin: $measure--small2 0;
    }
}

.slider.doc-slider {
    list-style: none;
    margin: 0 auto;

    li {
        height: $measure--large2;
        width: 100%;
        border: $border-width-default $border-style-solid $color-nearblack;
    }
}
