////
/// Page
/// @group base
////
html {
    background-color: $color-bg;
    color: $color-text;
    box-sizing: border-box;
    min-height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    scroll-behavior: smooth;

    @include media('reducemotion') {
        scroll-behavior: auto;
    }
}

body {
    min-height: 100vh;
    position: relative;
    scroll-behavior: smooth;

    @include media('reducemotion') {
        scroll-behavior: auto;
    }
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

.logo {
    display: block;
    width: $measure--medium7;
}

@mixin default-margin() {
    margin-left: $measure--basic;
    margin-right: $measure--basic;
    max-width: $width--max;

    @include media('>between') {
        margin-left: $measure--small2;
        margin-right: $measure--small2;
    }
}

@mixin default-padding() {
    padding-left: $measure--small2;
    padding-right: $measure--small2;

    @include media('>between') {
        padding-left: $measure--small4;
        padding-right: $measure--small4;
    }
}

@mixin standard-box() {
    @include default-margin();
    @include default-padding();

    @include media('>large') {
        margin-left: auto;
        margin-right: auto;
    }
}

main {
    article {
        background-color: $color-purewhite;
        grid-column: 1/-1;
        @include standard-grid();
        @include standard-box();
        @include box-shadow();

        .content-wrapper {
            grid-column: 1/-1;
        }

        @include media('>=small') {
            grid-column: 4/-4;
            .content {
                grid-column: 6/-6;
            }
            &.error {
                display: grid;
                .content {
                    grid-column: 6/-6;
                    margin: $measure--small4 0;
                }
            }
        }
    }

    .navigation--secondary {
        grid-column: 1 / span 6;

        & + article {
            grid-column: 7 / span 18;
            @include media('>=small') {
                margin-left: 0;
                margin-right: 0;
            }
        }
    }
}
